  height: 10px;
  background: #000;
  border-radius: 0 0 15px 15px;
  margin: auto;
  position: absolute;
  left: 0;
  right: 0;
}

.beard:after {
  content: "";
  width: 26px;
  height: 20px;
  background: #fff;
  border-radius: 100%;
  margin: auto;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  transform: translate(0, 50%);
}

.mouth {
  width: 30px;
  height: 14px;
  background: #000;
  border-radius: 0 0 15px 15px;
  margin: auto;
  position: absolute;
  top: 17px;
  left: 0;
  right: 0;
}

.tongue {
